
import {
  defineComponent, onBeforeUnmount, onMounted, PropType, ref, computed,
} from 'vue';

export interface SelectOption<T = unknown> {
  label: string;
  value: T;
}

export default defineComponent({
  name: 'Select',
  emits: ['update:value'],
  props: {
    label: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    value: {
      type: [String, Number, Boolean],
      default: null,
    },
    options: {
      type: Array as PropType<SelectOption[]>,
      required: true,
    },
  },
  setup(props, { emit }) {
    const opened = ref(false);
    const select = ref<HTMLDivElement | null>(null);

    const activeItem = computed(() => props
      .options
      .find((item) => item.value === props.value));

    const handleChange = (value: unknown) => {
      opened.value = false;
      emit('update:value', value);
    };

    const openDropdown = () => {
      if (!opened.value) opened.value = true;
    };

    const toggleDropdown = () => {
      opened.value = !opened.value;
    };

    const handleClickOutside = (e: MouseEvent) => {
      if (
        select.value
        && opened.value
        && e.target instanceof Node
        && e.target !== select.value
        && !select.value.contains(e.target)
      ) {
        opened.value = false;
      }
    };

    onMounted(() => {
      document.addEventListener('click', handleClickOutside, { capture: true });
    });

    onBeforeUnmount(() => {
      document.removeEventListener('click', handleClickOutside);
    });

    return {
      opened,
      select,
      activeItem,
      openDropdown,
      toggleDropdown,
      handleChange,
    };
  },
});
