<div class="select" :class="{opened}"><div class="select__head" @keyup.tab="openDropdown" @keypress.enter.space="toggleDropdown" @click="toggleDropdown" :tabindex="0"><div class="select__label">{{label}}</div><div class="select__value" :class="{placeholder: !activeItem}">{{activeItem?.label || placeholder}}</div><div class="select__shevrone" :class="{opened}"><svg width="12" height="8" viewBox="0 0 12 8" fill="none" xmlns="http://www.w3.org/2000/svg">
    <path fill-rule="evenodd" clip-rule="evenodd" d="M5.65685 4.24264L1.41421 0L0 1.41421L5.65685 7.07107L11.3137 1.41421L9.8995 0L5.65685 4.24264Z" fill="#333333"/>
</svg>
</div></div><div class="select__body" v-show="opened"><div class="select__option" v-for="option of options" @click="handleChange(option.value)" :key="option.value">{{option.label}}</div></div></div>