{{$t('page.addOperation')}}</Button></div><table class="table"><thead class="table-head"><tr class="table-row"><th class="table-cell table-header" v-for="column of columns" :class="{active: column === lastSortField, desc: column === lastSortField &amp;&amp; isDescSort}" :key="column" @click="sortTable(column)">{{ $t(`titles.${column}`) }}</th></tr></thead><tbody class="table-body"><tr class="table-row" v-for="row in preparedData" :key="preparedData.id" @click="open(row)"><td class="table-cell" v-for="column of columns" :key="`${column}_${preparedData.id}`"><StatusBadge v-if="column === 'assessment'" :level="row[column]"></StatusBadge><div class="table-content" v-else-if="column === 'date'" :class="column">{{getPrettyDateString(row[column])}}</div><div class="table-content" v-else-if="column === 'type'" :class="column">{{$t(`types.${getOperationType(row[column])}`)}}</div><div class="table-content" v-else :class="column">{{ row[column] }}</div></td></tr></tbody></table>