
import { defineComponent, PropType } from 'vue';

export enum ButtonActiveColor {
  RED = 'red',
  YELLOW = 'yellow',
  GREEN = 'green'
}

export default defineComponent({
  name: 'Button',
  emits: ['click'],
  props: {
    activeColor: {
      type: String as PropType<ButtonActiveColor>,
      default: null,
    },
    active: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
  },
  setup(props, { emit }) {
    return {
      emit,
    };
  },
});
